@use '../helpers/variables' as *;

.photo_gallery_title__decor {
	position: relative;
	padding-left: 17.3rem;
	margin-bottom: 3.6rem;
}

.photo_gallery_title__decor::before {
	content: '';
	position: absolute;
	top: 3rem;
	left: 1rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}

.photo_gallery__descr {
	text-align: center;
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 3rem;
	color: $color_descr_1;
	margin-bottom: 10rem;
}

.img_wrapper {
	display: flex;
	flex-wrap: wrap;
	row-gap: 6rem;
	column-gap: 6rem;
}

.img_wrapper_in {
	width: 433px;
	height: 313px;
}

.photo_gallery__img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
}
